:root {
  --pink: #ff1a8c;
  --bg-light: #fff7fa;
}

.section {
  background: linear-gradient(180deg, #fff, #ffe6f0, var(--bg-light));
  padding: 20px 20px;
  position: relative;
  overflow: hidden;
}

.inner {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
  align-items: start;
}

.content {
  background: transparent;
}

.title {
  color: var(--pink);
  font-family: var(--font-playfair, serif);
  font-size: 2.4rem;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 12px;
  text-shadow: 0 3px 12px rgba(255, 26, 140, 0.25);
}

.list {
  margin: 18px 0 18px 26px;
  list-style: disc;
  color: #4a3a3a;
  font-size: 1.05rem;
  line-height: 1.6;
}

.conclusion {
  margin-top: 20px;
  color: #4a3a3a;
  font-weight: 600;
  font-size: 1.1rem;
}

.butterfly {
  position: absolute;
  top: -10px;
  left: 18px;
}

.bookImageSmall {
  width: 300px;
  height: 350px;
  border-radius: 12px;
  box-shadow: 0 18px 44px rgba(0, 0, 0, 0.12);
  transition: transform 0.25s ease;
}

.bookImageSmall:hover {
  transform: scale(1.05);
}

@media (min-width: 768px) {
  .inner {
    grid-template-columns: 1fr 360px;
  }
  .bookPricing {
    flex-direction: column;
    align-items: flex-start;
  }
}
